import React, { useState, useEffect } from "react";
import ArticleGrid from "./ArticleGrid";
import { Link } from "react-router-dom";
import StarredArticles from "./StarredArticles";
import "bootstrap/dist/css/bootstrap.min.css";
import "../styles.css";
import logoWatermark from "./img/logo.png";
import styled from "styled-components";
import logo from "./img/logonav.png";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Container = styled.div`
  color: ${(props) => (props.darkMode ? "#fff" : "#222")};
  background-image: url(${logoWatermark});
  background-repeat: repeat;
  min-height: 100vh;
`;
const displayPopupMessage = (message) => {
  toast.info(message, {
    autoClose: 2000,
    hideProgressBar: false,
    closeButton: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: false,
    progress: undefined,
  });
};
const NewsApp = ({ onLogout }) => {
  const [news, setNews] = useState([]);
  const [starredArticles, setStarredArticles] = useState([]);
  const [token, setToken] = useState(null);
  const [userId, setUserId] = useState(null);
  const [sidebarExpanded, setSidebarExpanded] = useState(false); // New state for sidebar expansion

  const apiKey = "7e37076f4c10468b8ba932870b434b0b";
  const apiUrl = `https://newsapi.org/v2/everything?q=covid&from=2022-01-01country=in&apiKey=${apiKey}&language=en`;

  useEffect(() => {
    fetchNews();
    // Retrieve JWT token and user ID from localStorage on component mount
    const storedToken = localStorage.getItem("token");
    const storedUserId = JSON.parse(localStorage.getItem("userId"));
    setToken(storedToken);
    if (storedUserId) {
      setUserId(storedUserId.token);
      setStarredArticles(storedUserId.starredArticles || []); // Set initial starred articles
    }
  }, []);

  const fetchNews = async () => {
    try {
      const response = await fetch(apiUrl);
      const data = await response.json();
      setNews(data.articles);
    } catch (error) {
      console.error("Error fetching news:", error);
    }
  };

  const handleStarArticle = (article) => {
    const { url, urlToImage, title, description } = article;
    if (url && urlToImage) {
      // Check if the article is already starred
      const isAlreadyStarred = starredArticles.some(
        (starredArticle) => starredArticle.url === url
      );
      if (isAlreadyStarred) {
        // Display a popup message indicating that the article is already starred
        displayPopupMessage("You have already starred the news article.");
      } else {
        const starredArticle = { url, urlToImage, title, description };
        setStarredArticles((prevStarredArticles) => [
          ...prevStarredArticles,
          starredArticle,
        ]);
        // Store the updated starred articles in localStorage
        const updatedUserId = {
          ...userId,
          starredArticles: [...starredArticles, starredArticle],
        };
        localStorage.setItem("userId", JSON.stringify(updatedUserId));
      }
    } else {
      console.error("Article URL or image URL is null or undefined");
    }
  };

  const handleUnstarArticle = (article) => {
    const { url } = article;
    setStarredArticles((prevStarredArticles) =>
      prevStarredArticles.filter((starredArticle) => starredArticle.url !== url)
    );
    // Update the starred articles in localStorage
    const updatedUserId = {
      ...userId,
      starredArticles: starredArticles.filter(
        (starredArticle) => starredArticle.url !== url
      ),
    };
    localStorage.setItem("userId", JSON.stringify(updatedUserId));
  };

  return (
    <Container>
      <div className={`news-app ${sidebarExpanded ? "sidebar-expanded" : ""}`}>
        <nav
          className="navbar navbar-expand-lg bg-body-tertiary"
          data-bs-theme="dark"
        >
          <div className="container-fluid">
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarNav"
              aria-controls="navbarNav"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <img
              src={logo}
              alt="Logo"
              className="navbar-brand mx-auto"
              style={{ width: "200px", height: "auto" }}
            />
            <div className="collapse navbar-collapse" id="navbarNav">
              <ul className="navbar-nav">
                <li className="nav-item">
                  <Link className="nav-link" to="/home">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/notes">
                    Notes
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <h1 className="head">Pandemiconium News Portal</h1>
        <div className="main-content">
          <ToastContainer />
          <ArticleGrid news={news} onStar={handleStarArticle} />
          <StarredArticles
            articles={starredArticles}
            onUnstar={handleUnstarArticle}
            onExpand={() => setSidebarExpanded(true)} // Pass a function to set sidebarExpanded to true
            onCollapse={() => setSidebarExpanded(false)} // Pass a function to set sidebarExpanded to false
          />
        </div>
      </div>
    </Container>
  );
};

export default NewsApp;
