import React, { useState, useRef, useEffect } from "react";
import DeleteForeverOutlinedIcon from "@mui/icons-material/DeleteForeverOutlined";
import ColorPicker from "./ColorPicker";

function Note({ id, title, text, color, deleteNote, saveNote, timestamp, isEdited }) {
  const [isEditing, setIsEditing] = useState(false);
  const [editedTitle, setEditedTitle] = useState(title);
  const [editedText, setEditedText] = useState(text);
  const [editedColor, setEditedColor] = useState(color);
  const textareaRef = useRef(null);
  const [textLength, setTextLength] = useState(text.length); // Track text length

  const handleTitleChange = (event) => {
    setEditedTitle(event.target.value);
  };

  const handleTextChange = (event) => {
    const newText = event.target.value;
    setEditedText(newText);
    setTextLength(newText.length); // Update text length
  };

  const handleColorChange = (newColor) => {
    setEditedColor(newColor);
  };

  const handleSave = () => {
    const newTimestamp = new Date().toLocaleString("en-US", {
      day: "2-digit",
      month: "short",
      year: "numeric",
      hour: "numeric",
      minute: "numeric",
    });
    saveNote(id, editedTitle, editedText, editedColor, newTimestamp);
    setIsEditing(false);
  };

  const handleNoteClick = () => {
    setIsEditing(true);
  };

  useEffect(() => {
    if (isEditing) {
      textareaRef.current.focus();
    }
  }, [isEditing]);

  return (
    <div
      className="note"
      style={{ backgroundColor: isEditing ? editedColor : color }}
      onClick={isEditing ? null : handleNoteClick}
    >
      {isEditing ? (
        <div className="d-flex flex-column">
          <input
            type="text"
            value={editedTitle}
            onChange={handleTitleChange}
            style={{
              fontSize: "20px",
              fontWeight: "bold",
              marginBottom: "10px",
              border: "none",
              backgroundColor: "transparent",
              width: "100%",
              outline: "none",
            }}
          />
          <div className="textarea-container">
            <textarea
              ref={textareaRef}
              value={editedText}
              onChange={handleTextChange}
              maxLength={1000}
              style={{
                width: "100%",
                resize: "none",
                overflowY: "auto",
                height: "150px",
                marginBottom: "0",
              }}
            />
            <div className="text-counter">
              {textLength}/1000
            </div>
          </div>
          <div className="d-flex align-items-center">
            <ColorPicker handleColorChange={handleColorChange} initialColor={editedColor} />
            <button className="btn btn-primary ml-2" onClick={handleSave}>
              Save
            </button>
          </div>
        </div>
      ) : (
        <>
          <div className="note__header">
            <h3 style={{ fontWeight: "bold" }}>{title}</h3>
          </div>
          <div className="note__body" style={{ overflowY: "auto", maxHeight: "150px" }}>
            {text}
          </div>
          <div className="note__footer" style={{ justifyContent: "space-between", alignItems: "flex-end" }}>
            <div className="text-counter">
              {text.length}/1000
            </div>
            <div className="note-timestamp">
              {timestamp}
              {isEdited && " (edited)"}
            </div> {/* Display timestamp and "(edited)" if applicable */}
            <DeleteForeverOutlinedIcon
              className="note__delete"
              onClick={() => deleteNote(id)}
              aria-hidden="true"
            />
          </div>
        </>
      )}
    </div>
  );
}

export default Note;
