import React, { useState, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";

function CreateNote({ titleHandler, textHandler, saveHandler, inputTitle, inputText, noteColor, changeTransparency }) {
  const [currentColor, setCurrentColor] = useState(noteColor);
  const [transparency, setTransparency] = useState(0.5); // Default transparency

  useEffect(() => {
    setCurrentColor(noteColor);
  }, [noteColor]);

  const changeColor = (newColor) => {
    setCurrentColor(newColor);
  };

  const handleTransparencyChange = (e) => {
    setTransparency(e.target.value);
    changeTransparency(e.target.value);
  };

  return (
    <div className="note" style={{ backgroundColor: `${currentColor} ${transparency}` }}>
      <input
        type="text"
        placeholder="Title"
        value={inputTitle}
        onChange={titleHandler}
        style={{
          fontSize: "20px",
          fontWeight: "bold",
          marginBottom: "10px",
          border: "none",
          backgroundColor: "transparent",
          width: "100%",
          outline: "none",
        }}
      />
      <textarea
        cols="10"
        rows="5"
        value={inputText}
        placeholder="Type...."
        onChange={textHandler}
        maxLength="1000"
        style={{ width: "100%", marginBottom: "10px" }}
      ></textarea>
      <div className="note__footer">
        <div className="dropdown">
          <button
            className="btn btn-secondary dropdown-toggle"
            type="button"
            id="dropdownMenuButton"
            data-bs-toggle="dropdown"
            aria-expanded="false"
          >
            Color
          </button>
          <div className="dropdown-menu" aria-labelledby="dropdownMenuButton">
            {/* Circle buttons for color selection with names */}
            <button
              className="dropdown-item"
              type="button"
              onClick={() => changeColor("rgba(255, 0, 0, 0.1)")}
            >
              <span
                className="color-circle"
                style={{
                  backgroundColor: "red",
                  marginRight: "5px",
                  borderRadius: "50%",
                  display: "inline-block",
                  width: "20px",
                  height: "20px",
                }}
              ></span>
              Red
            </button>
            <button
              className="dropdown-item"
              type="button"
              onClick={() => changeColor("rgba(0, 128, 0, 0.1)")}
            >
              <span
                className="color-circle"
                style={{
                  backgroundColor: "green",
                  marginRight: "5px",
                  borderRadius: "50%",
                  display: "inline-block",
                  width: "20px",
                  height: "20px",
                }}
              ></span>
              Green
            </button>
            <button
              className="dropdown-item"
              type="button"
              onClick={() => changeColor("rgba(0, 0, 255, 0.1)")}
            >
              <span
                className="color-circle"
                style={{
                  backgroundColor: "blue",
                  marginRight: "5px",
                  borderRadius: "50%",
                  display: "inline-block",
                  width: "20px",
                  height: "20px",
                }}
              ></span>
              Blue
            </button>
            <button
              className="dropdown-item"
              type="button"
              onClick={() => changeColor("rgba(255, 255, 0, 0.1)")}
            >
              <span
                className="color-circle"
                style={{
                  backgroundColor: "yellow",
                  marginRight: "5px",
                  borderRadius: "50%",
                  display: "inline-block",
                  width: "20px",
                  height: "20px",
                }}
              ></span>
              Yellow
            </button>
            <button
              className="dropdown-item"
              type="button"
              onClick={() => changeColor("rgba(128, 0, 128, 0.1)")}
            >
              <span
                className="color-circle"
                style={{
                  backgroundColor: "purple",
                  marginRight: "5px",
                  borderRadius: "50%",
                  display: "inline-block",
                  width: "20px",
                  height: "20px",
                }}
              ></span>
              Purple
            </button>
          </div>
        </div>
        <button className="btn btn-primary" onClick={saveHandler}>
          Save
        </button>
        {/* Display small timestamp */}
        <div className="note-timestamp-small">
          {new Date().toLocaleString()}
        </div>
      </div>
    </div>
  );
}

export default CreateNote;
