import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import "./css/Note.css";
import "./css/CustomScrollbar.css";
import CreateNote from "./NoteComponents/CreateNote";
import Header from "./NoteComponents/Header";
import Note from "./NoteComponents/Note";
import { v4 as uuid } from "uuid";
import logoWatermark from "./img/logo.png";
import styled from "styled-components";
import logo from "./img/logonav.png";
const Container = styled.div`
  color: ${(props) => (props.darkMode ? "#fff" : "#222")}; /* Text color */
  background-image: url(${logoWatermark});
  background-repeat: repeat; /* Repeat the watermark across the entire background */
  height: 100vh;
`;

const NotesApp = ({ onLogout, darkMode }) => {
  // States
  const [notes, setNotes] = useState([]);
  const [inputText, setInputText] = useState("");
  const [inputTitle, setInputTitle] = useState(""); // Add inputTitle state
  const [noteColor, setNoteColor] = useState("rgba(44, 36, 36, 0.25)"); // Add noteColor state
  const [noteTransparency, setNoteTransparency] = useState(0.85);
  const [showScrollButton, setShowScrollButton] = useState(false);
  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setShowScrollButton(true);
      } else {
        setShowScrollButton(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  const scrollToTop = () => {
    window.scrollTo({
      top: 0,
      behavior: "smooth",
    });
  };
  // Handler to update inputText state
  const textHandler = (e) => {
    setInputText(e.target.value);
  };

  // Handler to update inputTitle state
  const titleHandler = (e) => {
    setInputTitle(e.target.value);
  };

  // Handler to update noteColor state
  const changeColor = (newColor) => {
    setNoteColor(newColor);
  };

  const formatTimestamp = (timestamp) => {
    const date = new Date(timestamp);
    const month = date.toLocaleString("default", { month: "short" }); // Get short month name
    const time = date.toLocaleTimeString([], {
      hour: "2-digit",
      minute: "2-digit",
    }); // Get hour and minute
    return `${date.getDate()} ${month} ${date.getFullYear()} ${time}`;
  };

  const changeTransparency = (newTransparency) => {
    setNoteTransparency(newTransparency);
  };
  // Handler to add a new note to the state array
  const saveHandler = () => {
    const newTimestamp = new Date().toLocaleString(); // Get current timestamp
    setNotes((prevNotes) => [
      ...prevNotes,
      {
        id: uuid(),
        title: inputTitle,
        text: inputText,
        color: noteColor, // Use the current color
        timestamp: newTimestamp, // Add timestamp to the note
      },
    ]);
    // Clear the inputs
    setInputTitle("");
    setInputText("");
  };

  // Handler to delete a note
  const deleteNote = (id) => {
    const filteredNotes = notes.filter((note) => note.id !== id);
    alert(" Are you sure you want to delete the note???");
    console.log(id);
    setNotes(filteredNotes);
  };

  const saveNote = (id, newTitle, newText, newColor, newTimestamp) => {
    setNotes((prevNotes) =>
      prevNotes.map((note) =>
        note.id === id
          ? {
              ...note,
              title: newTitle,
              text: newText,
              color: newColor,
              timestamp: newTimestamp,
            }
          : note
      )
    );
  };

  return (
    <>
      <nav
        className="navbar navbar-expand-lg bg-body-tertiary"
        data-bs-theme="dark"
      >
        <div className="container-fluid">
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <img
            src={logo}
            alt="Logo"
            className="navbar-brand mx-auto"
            style={{ width: "200px", height: "auto" }}
          />
          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav">
              <li className="nav-item">
                <Link className="nav-link" to="/home">
                  Home
                </Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link" to="/news">
                  News
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
      <Container darkMode={darkMode}>
        <Header />
        <div className="notes">
          {notes.map((note) => (
            <Note
            key={note.id}
            id={note.id}
            title={note.title}
            text={note.text}
            color={note.color}
            timestamp={formatTimestamp(note.timestamp)} // Format timestamp
            deleteNote={deleteNote}
            saveNote={saveNote}
            noteColor={noteColor} // Pass noteColor state
            changeColor={changeColor} // Pass changeColor function
          />
          ))}
          <CreateNote
            titleHandler={titleHandler}
            textHandler={textHandler}
            saveHandler={saveHandler}
            inputTitle={inputTitle}
            inputText={inputText}
            noteColor={noteColor}
            changeColor={changeColor}
            changeTransparency={changeTransparency}
          />
        </div>
        {showScrollButton && (
          <button
            className="btn btn-primary"
            onClick={scrollToTop}
            style={{ position: "fixed", bottom: "20px", right: "20px" }}
          >
            Back to Top
          </button>
        )}
      </Container>
    </>
  );
};

export default NotesApp;
