import React, { useState } from "react";
import Article from "./Article";
import { CSSTransition } from "react-transition-group";

const StarredArticles = ({ articles, onUnstar, onExpand, onCollapse }) => {
  const [expanded, setExpanded] = useState(true);

  const toggleExpand = () => {
    setExpanded(!expanded);
    if (!expanded) {
      onExpand(); // Call onExpand when expanding the sidebar
    } else {
      onCollapse(); // Call onCollapse when collapsing the sidebar
    }
  };

  return (
    <>
      <div
        className={`starred-articles ${expanded ? "expanded" : "collapsed"}`}
      >
        <h2 style={{ color: "#000" }}>Starred Articles ({articles.length})</h2>
        <CSSTransition
          in={expanded}
          timeout={300}
          classNames="slide"
          unmountOnExit
        >
          <div className="grid">
            {articles.map((article) => (
              <Article
                key={article.title}
                article={article}
                onUnstar={onUnstar}
                isStarred={true}
              />
            ))}
          </div>
        </CSSTransition>
      </div>
      <div className="sidebar-toggle" onClick={toggleExpand}>
        {expanded ? <span>&#9668;</span> : <span>&#9658;</span>}
      </div>
    </>
  );
};

export default StarredArticles;
